<template>
  <div class="upload-box">
    <div class="avatar-uploader-box">
      <!-- 方框样式 -->
      <div class="upload-wrapper" v-if="!(!isMulti && imageUrl.length > 0)">
        <el-upload
        ref="avatarUploader"
        class="avatar-uploader"
        action="string"
        :show-file-list="false"
        :on-change="handleAvatarChange"
        :auto-upload="false"
        accept="image/*"
        >
          <span
            v-if="!(!isMulti && imageUrl.length > 0)"
            v-loading="loading"
            element-loading-text="上传中"
            element-loading-spinner="el-icon-loading"
            element-loading-background="rgba(0, 0, 0, 0.8)"
            style="line-height: 30px"
          >
           <i class="el-icon-plus avatar-uploader-icon"/>
         </span>
          <div class="upload_tip" slot="tip">{{tip}}</div>
        </el-upload>
      </div>
      
      <div class="image-wrapper" v-if="imageUrl.length > 0">
        <li class="item-wrapper" v-for="(item, index) in imageUrl" :key="index" @mouseover.stop="isShowPopup = true; showIndex = index">
          <!-- 上传的图片 -->
          <img
            style="width: 100%; height: auto;"
            :src="item"
            class="avatar"
          />

          <!-- 显示查看和删除的按钮弹窗 -->
          <div
            v-show="showIndex === index && isShowPopup"
            class="avatar-uploader-popup"
            @mouseleave="isShowPopup = false; showIndex = ''"
          >
            <i class="el-icon-zoom-in" @click="dialogVisible = true; previewUrl = item" />
            <i class="el-icon-delete" @click="delImageUrl(index)" />
          </div>
      </li>
      </div>
    </div>
    <!-- 查看图片 -->
    <el-image-viewer
      v-if="dialogVisible"
      :on-close="closeViewer"
      :url-list="[previewUrl]"
    />

    <!-- 裁剪图片 start-->
    <vue-cropper ref="VueCropperItem" :size="size" @confirm="confirmCropper" />
    <!-- 裁剪图片 end-->
  </div>
</template>

<script>
import VueCropper from './vue-cropper.vue'
import ElImageViewer from 'element-ui/packages/image/src/image-viewer.vue'

export default {
  name: 'ImagePicker',
  components: { VueCropper, ElImageViewer },
  watch: {
    imageUrl: {
      handler(val) {
        if (val) {
          if(this.isMulti) {
            this.$emit('input', val)
          } else {
            this.$emit('input', val[0])
          }
        } else {
          this.$emit('input', '')
        }
      }
    }
  },
  mounted() {
    if (this.value) {
      this.imageUrl = [this.value];
    } else {
      this.imageUrl = [];
    }
  },
  props: {
    isMulti: {
      type: Boolean,
      default: false
    },
    isCut: {
      type: Boolean,
      default: false
    },
    // 上传图片接口, 返回Promise
    uploadImage: {
      type: Function,
      required: true
    },
    // 限制大小,单位是MB
    maxSize: {
      type: Number,
      default: 0
    },
    // 提示
    tip: {
      type: String,
      default: ''
    },
    // 双向绑定
    value: {
      type: String,
      default: "",
    },
    //尺寸
    size: {
      type: Array,
      default: () => {
        return [1280, 720];
      },
    }
  },
  data() {
    return {
      loading: false,
      isShowPopup: false,
      dialogVisible: false,
      isProgress: false,
      percentage: 0,
      imageUrl: [],
      showIndex: '',
      previerUrl: ''
      };
  },
  methods: {
    // 上传之前验证是否师傅条件
    beforeAvatarUpload(file) {
      return new Promise((resolve, reject) => {
        if (!/\.(jpg|jpeg|png|JPG|PNG)$/.test(file.name)) {
          reject("上传图片只能是JPG或PNG格式!");
        }

        // 判断大小
        if(this.maxSize && file.size > this.maxSize * 1024 * 1024) {
          reject(`上传图片大小不能超过${this.maxSize}M!`);
        } 
        
        resolve("符合表單規則");
      });
    },
    // 上传改变
    handleAvatarChange(file, fileList) {
      this.beforeAvatarUpload(file).then(() => {
        if(this.isCut) {
          // 需要裁剪
          this.showVueCropperItem(file)
        } else {
          // 不需要裁剪
          this.confirmCropper(file)
        }
        
      }).catch(err => {
        this.$message.error(err)
      })
    },
    // 删除图片
    delImageUrl(index) {
      this.imageUrl.splice(index, 1)
    },
    // 显示裁剪框
    showVueCropperItem(file) {
      this.$refs.VueCropperItem.init();
      this.$refs.VueCropperItem.fileName = file.name;

      this.$refs.VueCropperItem.options.img = URL.createObjectURL(file.raw);
      // this.setVueCropperOptions();
    },
    // 确认裁剪后上传
    confirmCropper(file) {
      this.loading = true;
      this.uploadImage(file).then(res => {
        // 上传成功
        this.isProgress = false;
        this.imageUrl.push(res)
        this.loading = false
      }).catch((err) =>{
        // 上传失败
        this.$message.error('上传失败请重试！')
        this.isProgress = false;
        this.loading = false
      })
    },
    // 设置裁剪的配置
    setVueCropperOptions() {
      this.$refs.VueCropperItem.options.autoCropWidth = this.autoCropWidth;
      this.$refs.VueCropperItem.options.autoCropHeight = this.autoCropHeight;
      this.$refs.VueCropperItem.options.fixedBox = this.fixedBox;
      this.$refs.VueCropperItem.isCompress = this.isCompress; // 是否压缩图片
      this.$refs.VueCropperItem.compress = this.compress; // 压缩率
    },
    closeViewer() {
      this.dialogVisible = false;
      this.previerUrl = ''
    },
  },
    }
</script>
<style lang="scss" scoped>

.upload-box {
  width: 100%;
  text-align: left;
  .avatar-uploader-box {
    position: relative;
    line-height: 0;
    width: fit-content; //  收缩与包裹,收缩到合适
    .upload-wrapper {
      margin-bottom: 10px;
      margin-right: 10px;
      display: inline-block;
      list-style: none;
      width: 120px;
      height: 120px;
      border: 1px dashed #726e6e;
      border-radius: 6px;
      color: #8c939d;
      &:hover {
          border-color: #409eff;
          color: #409eff;
        }
      .avatar-uploader {
      /deep/ .el-upload {
        cursor: pointer;
        position: relative;
        overflow: hidden;
      }

      .avatar-uploader-icon {
        font-size: 26px;
        width: 120px;
        height: 120px;
        line-height: 120px;
        text-align: center;
      }
    }
  }
    .avatar {
      width: 120px;
      height: auto;
      display: block;
    }
    .progress-box {
      position: absolute;
      top: 0;
      left: 0;
      z-index: 2;
      width: 99%;
      height: 99%;
      background: #fff;
      display: flex;
      justify-content: center;
      align-items: center;
    }

    .avatar-uploader-popup {
      position: absolute;
      top: 0;
      left: 0;
      z-index: 9;
      width: 100%;
      height: 100%;
      background: rgba($color: #000000, $alpha: 0.5);
      display: flex;
      justify-content: center;
      align-items: center;
      color: #fff;
      font-size: 20px;
      border-radius: 6px;
      i {
        width: 30%;
        text-align: center;
        padding: 0 5%;
        font-size: 24px;
      }
    }
  }
  .upload_tip {
    margin-top: 15px;
  }
  .image-wrapper {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
    &::after {
        content: "";
        width: 120px;
    }
    .item-wrapper {
      position: relative;
      margin-right: 5px; 
      margin-bottom: 5px;
      display: inline-flex;
      justify-content: center;
      align-items: center;
      list-style: none;
      width: 120px;
      height: 120px;
      border: 1px dashed #726e6e;
      border-radius: 6px;
      color: #8c939d;
      &:hover {
          border-color: #409eff;
          color: #409eff;
      }
    }
  }
}
</style>


